.Experts {
    margin: 4rem 6rem;
    color: #2a2a2a;
}

.Experts h1 {
    font-size: 3rem;
}

.Expertscard {
    margin-top: 3rem;
    display: flex;
    justify-content: space-between;
}

.e-card {
    width: 32%;
    text-align: start;
    box-shadow: 0 5px 25px 2px rgba(0, 0, 0, 0.11);
    border-radius: 7px;
    cursor: pointer;
    padding: 1rem 0.8rem;
}

.e-image {
    height: 200px;
    overflow: hidden;
    border-radius: 7px;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #eeeeeeb8;
}

.e-image img {
    width: 80%;
    height: 80%;
    transition: 0.3s ease-in-out;
    border-radius: 7px;
}

.e-image:hover img {
    transform: scale(1.1);
}

.e-card h4 {
    font-size: 1.3rem;
    padding: 0.9rem 0 0.2rem 0;
}

@media screen and (max-width:850px){
    .Experts {
        margin: 4rem 2rem;
    }

    .Expertscard {
        flex-direction: column;
    }

    .e-card {
        width: 100%;
        margin-bottom: 1.5rem;
    }
}