.Job {
    margin: 3rem 6rem;
    color: #2a2a2a;
}

.Jobcard {
    margin-top: 3rem;
}

.j-card {
    width: 100%;
    text-align: start;
    box-shadow: 0 5px 25px 2px rgba(0, 0, 0, 0.11);
    border-radius: 7px;
    cursor: pointer;
    padding: 1.8rem 0.1rem;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    margin-bottom: 2.5rem;
}

.j-image {
    height: 150px;
    overflow: hidden;
    border-radius: 7px;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #eeeeeeb8;
}

.j-info {
    width: 70%;
}

.j-attr {
    margin-top: 1.5rem;
    display: flex;
    justify-content: space-evenly;
}

.j-attr h5 {
    color: #fff;
    background-color: #01959a;
    font-weight: 600;
    letter-spacing: 1px;
    font-size: 0.8rem;
    padding: 0.5rem 2rem;
    border-radius: 5px;
}

.j-image img {
    width: 80%;
    height: 80%;
    transition: 0.3s ease-in-out;
    border-radius: 7px;
}


.j-image:hover img {
    transform: scale(1.1);
}

.j-card h4 {
    font-size: 1.5rem;
    padding: 0.9rem 0 0.2rem 0;
}

@media screen and (max-width:850px){

    .Job {
        margin: 2rem 1rem;
    }
    
    .Jobcard {
        margin-top: 1rem;
    }
    
    .j-card {
        padding: 0rem 0.1rem;
    }
    
    .j-image {
        height: 60px;
    }
    
    .j-info {
        width: 100%;
        padding: 0 0.5rem;
    }

    
    .j-card h4 {
    font-size: 0.8rem;
    }

    .j-card p {
    font-size: 0.6rem;
    font-weight: 500;
    }
    
    .j-attr {
        margin-top: 0.1rem;
        margin-bottom: 0.5rem;
    }
    
    .j-attr h5 {
        color: #fff;
        background-color: #01959a;
        font-weight: 400;
        margin-top: 0.7rem;
        letter-spacing: 1px;
        font-size: 0.5rem;
        padding: 0.1rem 0.2rem;
        border-radius: 3px;
    
    }
    
    .j-image img {
        width: 100%;
        height: 60%;
    }
    

}

@media screen and (max-width:375px) {
    .j-attr {
        display: none;
    }

    .j-info {
        width: 100%;
        padding: 1.5rem 0.5rem;
    }

    .j-image {
        height: 80px;
    }
}